    },
    methods: {
			async clickRabbit(){
				//生成福兔
				this.clickFlag = false
			  try {			
					this.isLoading = true
					let prompt = "一只兔子，它有如下特征："+this.bodyx+"、"+this.ear+"、"+this.skin+"，身穿"+this.cloth+"，图片背景是"+this.back+'，'+this.style+"风格"
					this.prompt = this.bodyx+"、"+this.ear+"、"+this.skin+"，身穿"+this.cloth+"，图片背景是"+this.back+'，'+this.style+"风格"
					this.axios.request({
							method: 'post',
							url: 'https://api.ilark.io/image',
							data:{
								prompt: prompt
							}
						})
						.then(arg => {
							this.isLoading = false
							console.log(688, arg)
							console.log(699, arg.data)
							this.image_url = arg.data.image_url
							
						})
						.catch(error => {
							this.showMask = true
							this.successFlag = false
							this.transWyoyoValue = ''
							this.maskInfo = "错误！\n" + error
						})
			  }catch(e){
						this.showMask = true
						this.successFlag = false
						this.transWyoyoValue = ''
						this.maskInfo = "错误！\n" + e
			    } 
		 },
		  more(){
			 this.clickFlag = true
			 this.image_url = ''
		  }, 
		 
      hideMask(){
        this.showMask=false
      },
			async waiting(){
				return new Promise(resolve => {
					setTimeout(resolve, 2000)  //2秒
				})
			},

    },

    components: {
      SmallLoading,
    },

		mounted() {
			this.isLoading = false

		},

    

    }
</script>

